
import './index.scss'
import Head from '~/components/Head'
import { connect } from 'react-redux'
import { getReaderList } from '../../actions'
import {dateFormat} from '&/methods'

@connect(
  state => {
    return {
      readerlist: state.getIn(['data', 'readerlist'])
    }
  }
)

export default class BorrowReader extends Component {


  componentDidMount() {
    this.props.dispatch(getReaderList({
      url: '/bookstore/getReaderList',
      params: {
        limit: 50
      }
    })).then(res=>{
      console.log(this.props.readerlist)
    })
  }

  render() {
    const { readerlist } = this.props
    console.log(readerlist)
    return (
      <div className='bookreader'>
        <Head title='用户管理' showBack={true}></Head>
        <div className='userlist'>
          <div>
            {
              readerlist.map((list, i) => {
                console.log(11111)
                return (
                  <div key={i} style={{ backgroundColor: i % 2 == 0 ? '#d4ddf0' : '#ddf4e1', padding: '5px 0' }}>
                    <div className='list' style={{ padding: '0 5px', boxSizing: 'border-box' }}>
                      <div className='l_left'>{list.readername}</div>
                      <div className='l_center' style={{color:'red'}}>{list.readertel}</div>
                      <div className='l_right'>借书：<span style={{ color: 'red' }}>{list.num}</span>本</div>
                    </div>
                    <div style={{ padding: '0 5px 5px 5px', boxSizing: 'border-box' }}>书名：{list.bookname}</div>
                    <div style={{ padding: '0 5px', boxSizing: 'border-box' }}>借书时间：<span style={{color:'red'}}>{dateFormat(list.time)}</span></div>
                  </div>
                )
              })
            }
          </div>

        </div>
      </div>
    )
  }
}